<template>
<div>
    <div class="panel panel-info">
        <div class="panel-body">
            <div>设备信息管理</div>
            <div class="buttonbox" v-show="!inforadmin&&!equipmentDeployAdmin">
                <div class="col-sm-1" v-if="$api.opreate.hasLoginComponents(this,'sys:userMan:userOpera')">
                    <select class="form-control" v-model="tableSelectData.pageloginData.state" @change="changeAudit">
                            <option value="0">未审核</option>
                            <option value="1">已审核</option>
                            <option value="2">正在运行</option>
                            <option value="3">已停止</option>
                        </select>
                </div>
                <button type="button" class="btn btn-info" @click="inforAddClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:page:add')">新增设备</button>
                <button type="button" class="btn btn-info" @click="equipmentDeployClick(true)" v-if="$api.opreate.hasLoginComponents(this,'sys:page:add')">设备部署</button>
                <button type="button" class="btn btn-warning" @click="updateData" v-if="$api.opreate.hasLoginComponents(this,'sys:page:modify')">修改</button>
                <button type="button" class="btn btn-danger" @click="deleteData" v-if="$api.opreate.hasLoginComponents(this,'sys:page:delete')">删除</button>
                <div class="pull-right col-sm-3 search-input" v-if="$api.opreate.hasLoginComponents(this,'sys:page:search')">
                    <div class="col-sm-12">
                        <div class="col-sm-8 search-input ">
                            <input type="text" class="form-control" placeholder="搜索用户">
                        </div>
                        <input type="button" value="搜索" class="btn btn-info col-sm-3" @click="searchBtnClick">
                    </div>
                </div>
            </div>
            <tables v-show="!inforadmin&&!equipmentDeployAdmin" :tableSelectData="tableSelectData"></tables>
            <div v-show="inforadmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                    <!-- 设备名 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备名</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('name')&&addData.equipment_name!='','has-error':errors.has('name')}">
                            <input type="text" class="form-control" name="name" :placeholder="'请输入设备名,最多为30位'" v-model="addData.equipment_name" :data-vv-as="'设备名'" v-validate="$api.opreate.validateAdd('name',30,stateid,opreate)">
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('name')">{{errors.first("name")}}</span>
                        </div>
                    </div>
                    <!-- 设备类型 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备类型</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.equipment_type_id"><option :value="item.equipment_type_id" v-for="(item,index) of equipment_type" :key="index" v-text="item.equipment_type_name"></option> </select>
                        </div>
                    </div>
                    <!-- 设备控制类型 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备控制类型</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.control_type_id"><option :value="item.control_type_id" v-for="(item,index) of control_type" :key="index" v-text="item.control_type_name"></option> </select>
                        </div>
                    </div>
                    <!-- 设备厂商 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备厂商</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addData.manufacturer_id"><option :value="item.manufacturer_id" v-for="(item,index) of manufacturer" :key="index" v-text="item.manufacturer_name"></option> </select>
                        </div>
                    </div>

                    <!-- 设备功能说明 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">设备功能说明</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('explain')&&addData.function_explain!='','has-error':errors.has('explain')}">
                            <textarea rows="6" class="form-control" name="explain" :placeholder="'请输入设备功能说明,最多为20位'" v-model="addData.function_explain" :data-vv-as="'设备功能说明'" v-validate="'required'"></textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('explain')">{{errors.first("explain")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="inforAddClick(false)">返回</button>
                    </div>
                </form>
            </div>
            <div v-show="equipmentDeployAdmin">
                <form class="form form-horizontal col-sm-8" ref="refForm">
                    <!-- 部署节点 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">部署节点</label>
                        <div class="col-sm-6">
                            <select type="text" name="url" class="form-control" v-model="addDeployData.deploy_node_id"><option :value="item.deploy_node_id" v-for="(item,index) of deploy_type" :key="index" v-text="item.deploy_type_name"></option> </select>
                        </div>
                    </div>
                    <!-- 设备功能说明 -->
                    <div class="form-group">
                        <label class="control-label col-sm-2">部署备注</label>
                        <div class="col-sm-6" :class="{'has-success':!errors.has('remarks')&&addDeployData.remarks!='','has-error':errors.has('remarks')}">
                            <textarea rows="6" class="form-control" name="remarks" :placeholder="'请输入部署备注'" v-model="addDeployData.remarks" :data-vv-as="'部署备注'" v-validate="'required'"></textarea>
                        </div>
                        <div class="text-danger">
                            <span v-show="errors.has('remarks')">{{errors.first("remarks")}}</span>
                        </div>
                    </div>
                    <div class="form-group col-sm-12">
                        <button type="button" class=" col-sm-2 col-sm-offset-2 btn btn-info" @click="addDeployDatas">提交</button>
                        <button type="button" class=" col-sm-2 col-sm-offset-2  btn btn-warning" @click="equipmentDeployClick(false)">返回</button>
                    </div>
                </form>
            </div>
        </div>
    </div>
</div>
</template>

设备信息
<script>
import tables from "@/components/table.vue";
export default {
    data: function () {
        return {
            inforadmin: false,
            opreate: "add",
            tableSelectData: {
                selectTableurl: "",
                pageloginData: {
                    id: this.stateid,
                    state: 0
                },
                filterName: []
            },
            addData: {
                equipment_type_id: "", //设备类型ID
                equipment_name: "", //设备名称
                control_type_id: "", //控制类型ID
                manufacturer_id: "", //厂商ID
                function_explain: "" //功能说明
            },
            addDeployData: {
                remarks: "", //备注
                deploy_node_id: "", //部署节点id
                equipment_id: "" //设备id
            },
            equipmentDeployAdmin: false,
            equipment_type: [], //设备类型
            control_type: [], //设备控制类型
            manufacturer: [], //设备厂商
            deploy_type: [], //部署节点

        }
    },
    props: {
        stateid: String,
        statetitle: String
    },
    computed: {
        checkData() {
            return this.$store.getters.GETUSERTABLEPAGECHECKDATA;
        }
    },
    components: {
        tables,
    },
    methods: {
        //部署节点
        equipmentDeployClick(bool) {
            var vm = this;
            if (bool) {
                if (vm.$api.opreate.TESTcheck(vm, "部署节点")) {
                    vm.addDeployData.equipment_id = vm.checkData[0].equipment_id;
                    vm.loginDeploy_type();
                }
            }
            vm.equipmentDeployAdmin = bool;
        },
        //添加按钮按下
        inforAddClick(bool) {
            this.inforadmin = bool;
            this.opreate = "add";
            for (var key in this.addData) {
                this.addData[key] = "";
            }
            if (bool) {
                this.loginAddequipInfor();
            }
            this.$validator.errors.clear();
        },
        // 修改按钮按下
        updateData() {
            var vm = this;
            if (vm.$api.opreate.TESTcheck(vm, "修改")) {
                vm.inforadmin = true;
                vm.opreate = "update";
                vm.addData = vm.checkData[0];
                vm.loginAddequipInfor();
            }
        },
        //调用添加和修改设备信息时获取信息的函数
        loginAddequipInfor() {
            this.loginControl_type();
            this.loginControl_type();
            this.loginManufacturer();
        },
        //添加函数
        addDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addData;
            var datas = {
                data: data,
                states: {
                    id: stateId
                }
            };
            // 修改操作
            var bool;
            if (vm.opreate == "update") {
                bool = vm.$api.http.opreateHttp(vm, "", datas, "修改", "");
            } else if (vm.opreate == "add") {
                //添加操作
                bool = vm.$api.http.opreateHttp(vm, "", datas, "添加", "");
            }
            setTimeout(function () {
                vm.$validator.errors.clear();
            }, 300)
            bool.then(function (success) {
                if (success) {
                    setTimeout(function () {
                        vm.inforAddClick(false);
                    }, 800);
                }
            })
        },
        //添加部署节点函数
        addDeployDatas() {
            var vm = this;
            var stateId = vm.stateid;
            var data = vm.addDeployData;
            var datas = {
                data: data,
                states: {
                    id: stateId
                }
            };
            bool = vm.$api.http.opreateHttp(vm, "", datas, "添加", "");
            setTimeout(function () {
                vm.$validator.errors.clear();
            }, 300)
            bool.then(function (success) {
                if (success) {
                    setTimeout(function () {
                        vm.equipmentDeployClick(false);
                    }, 800);
                }
            })
        },
        // 删除按钮按下
        deleteData() {
            var vm = this;
            var checkData = vm.$store.getters.GETUSERTABLEPAGECHECKDATA;
            if (vm.$api.opreate.TESTcheck(vm, "删除")) {
                //询问框
                layer.confirm(
                    "是否删除", {
                        btn: ["确认", "取消"] //按钮
                    },
                    function () {
                        var datas = {
                            id: checkData[0].id,
                            states: {
                                id: vm.stateid
                            }
                        };
                        vm.$api.http.opreateHttp(
                            vm,
                            "",
                            datas,
                            "删除"
                        );
                    }
                );
            }
        },
        //搜索按钮按下
        searchBtnClick() {
            var vm = this;
            if (vm.searchData.data != "") {} else {
                vm.$layer.msg("请输入场景地址名后搜索", {
                    icon: 0
                });
            }
        },
        //调换审核状态查看
        changeAudit() {
            var vm = this;
            vm.$store.commit("REININTPAGE");
            vm.$api.http.loginTableData(
                vm,
                "selectUserPaging.action",
                vm.tableSelectData.pageloginData
            );
        },
        //获取设备类型信息
        loginEquipment_type() {
            var vm = this;
            vm.$axios.post(vm.HOST + "").then(function (data) {
                if (data.bool) {
                    vm.equipment_type = data.data.list;
                }
            }).catch(function (error) {
                console.log(error)
            })
        },
        //获取设备控制类型信息
        loginControl_type() {
            var vm = this;
            vm.$axios.post(vm.HOST + "").then(function (data) {
                if (data.bool) {
                    vm.control_type = data.data.list;
                }
            }).catch(function (error) {
                console.log(error)
            })
        },
        // 获取设备厂商信息
        loginManufacturer() {
            var vm = this;
            vm.$axios.post(vm.HOST + "").then(function (data) {
                if (data.bool) {
                    vm.manufacturer = data.data.list;
                }
            }).catch(function (error) {
                console.log(error)
            })
        },
        // 获取设备部署节点信息
        loginDeploy_type() {
            var vm = this;
            vm.$axios.post(vm.HOST + "").then(function (data) {
                if (data.bool) {
                    vm.deploy_type = data.data.list;
                }
            }).catch(function (error) {
                console.log(error)
            })
        }
    }
};
</script>

<style>

</style>



